*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
body{
    background-color: skyblue;
    font-family: sans-serif;
}
h2{
    text-align: center;
    font-weight: 600;
    margin-bottom: 20px;
}
.container{
    width: 400px;
    height: 500px;
    margin: 10px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color:aqua;
    box-shadow: 0 10px 25px rgba(145, 245, 467, 540.1);
    border-radius: 20px;
    padding: 40px;
}
form{
    display: flex;
    flex-direction: column;
}
input {
    margin-bottom: 20px;
    padding: 10px;
    box-shadow: 3px 3px 5px grey;
    font-size: 14px;
    font-weight: 600;
    width: 300px;
  }
label{
    text-align: center;
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: .4rem;
    color:black;
    transition: all 0.3s;
    pointer-events: none;
    padding: 5%;
    margin: left 1px; 
    display: inline-block;
    border-radius: 3px;
  }
  
  button {
    padding: 10px;
    border: 1px solid gray;
    background-color:#B4886B;
    border-radius: 5px;
    font: inherit;
    box-shadow: 3px 3px 5px black;
  }
  button:hover {
    background-color: orange;
    box-shadow: 3px 3px 5px grey;
  }
  button:focus {
    outline: 2px solid gray;
    font-weight: bold;
    color: white;
  }
  

